<template>
<div id="container">
<h1> Faisal Notes 2021 </h1>
    <div id="main">
        <div id="inputer">
            <input type="text"  placeholder="write note"  v-model="entry" @keyup.enter="pressme(entry)">
        </div>
        <div id="displayer">
            <li v-for="(valve, id) in arr" :key="id">
                <p>{{valve}}</p> 
                <button @click="deleter(id)">X</button>
            </li>
        </div>
    </div>
</div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue.use(VueAxios, axios)


export default {
    data() {
        return {
            entry:"",
            arr:[],
            
        }
    },

    async mounted(){
      
      if(localStorage.getItem('notes')){
        this.arr= JSON.parse(localStorage.getItem('notes'));
      }
      else
        {
        let list =await axios.get('https://phplaravel-598798-1936248.cloudwaysapps.com/api/index/21').then((resp)=>resp.data.notes);
        this.arr=JSON.parse(list);
        this.savedata();
        }
    },

    methods: {

       pressme(entry){
            this.arr.push(entry);
            this.entry= "";
            this.savedata();
            //await axios.post('http://127.0.0.1:8000/api/add',{notes:this.notes});
            //let list = await axios.get('http://127.0.0.1:8000/api/index');
           // this.arr= list.data;
        },

        deleter(id){
            this.arr.splice(id,1);
            this.savedata();
           
            //await this.axios.delete('http://127.0.0.1:8000/api/delete/'+id);
            //let list = await axios.get('http://127.0.0.1:8000/api/index');
            //this.arr= list.data;
        },

        savedata(){
            const arrow= JSON.stringify(this.arr);
            localStorage.setItem("notes", arrow);
            this.syncdata(arrow);
            
        },

        async syncdata(arrow){
            await this.axios.put('https://phplaravel-598798-1936248.cloudwaysapps.com/api/update',
            {
                "id": "21",
                "notes": arrow,
            
            });
        }

    },
}
</script>

<style scoped>

#container{
    margin:10px auto;
       width: 450px;
    text-align: center;
    border: 1px solid rgb(112, 209, 112);
    padding: 1px;
}
#container h1{
    color: white;
    border-bottom: 1px solid rgb(255, 255, 255);
    background-color: #40c362;
}

#main{
    padding:10px;
    background-color: palegreen;
 
}

#displayer{
    text-align: left;
    margin-top:15px ;
    
}

#displayer li{
    list-style: none;
    background-color: rgb(217 255 201);
    margin: 8px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    
}

#displayer p {
        padding: 10px;
        margin: 0px;
        width:95%;
}

#displayer button{
    float: right;
   
    font-size: 24px;
    background: #e6051a;
    padding: 4px;
    font-family: cursive;
    color: white;
}


input{
    background: white;
    padding: 7px;
    margin: 10px;
    border-radius: 13px;
    width: 95%;
    font-size: 20px;
    height: 45px;
}
</style>



